import Paper from "paper";

const drawGraph = () => {
  let graphLayer = new Paper.Layer()

    var drawGridLines = function(width, height, boundingRect) {
        var width_per_rectangle = boundingRect.width / width;
        var height_per_rectangle = boundingRect.height / height;
        var hatchLengthFactor = .012;	
        
      
        //set up vertical grid lines
          for (var i = 0; i <= width; i++) {
          
          var xPos = boundingRect.left + i * width_per_rectangle;
              var topPoint = new Paper.Point(xPos, boundingRect.top);
              var bottomPoint = new Paper.Point(xPos, boundingRect.bottom);
              var aLine = new Paper.Path.Line(topPoint, bottomPoint);
          aLine.strokeColor = 'black';
          aLine.strokeWidth = .6;
      
          //fix left and right vertical lines being cut off
          if(i === 0){
            aLine.strokeWidth = 2;
          }
          if(i === width){
            aLine.strokeWidth = 2.5;
          }
          //
      
          //vertical center line and hatch marks
          if(i === 5){
            aLine.strokeWidth = 1.5;
      
            //create increments to space out hatch marks evenly
            var incy = boundingRect.width / width*(8/5)/10;
      
            //already have xPos
            var yPos = boundingRect.top;
            var oldyPos = yPos;
            var oldxPos = xPos;
            
            //create hatch marks for vertical center line
            for(var hatchMark = 1; hatchMark <= 40; hatchMark++){
              xPos = oldxPos;
              yPos = oldyPos;
      
              yPos = yPos+hatchMark*incy;
              xPos = xPos*(1-hatchLengthFactor); //make a point to the left of the center line
              var hatch1 = new Paper.Point(xPos,yPos);
              xPos=xPos*(1+2*hatchLengthFactor); //make a point to the right of the center line
              var hatch2 = new Paper.Point(xPos,yPos);
              var connect = new Paper.Path.Line(hatch1,hatch2);
              connect.strokeColor = 'black';
              connect.strokeWidth = .8;
            }
          }
          }
      
        //set up horizontal grid lines
          for (i = 0; i <= height; i++) {
              yPos = boundingRect.top + i * height_per_rectangle;
              var leftPoint = new Paper.Point(boundingRect.left, yPos);
              var rightPoint = new Paper.Point(boundingRect.right, yPos);
              aLine = new Paper.Path.Line(leftPoint, rightPoint);
          aLine.strokeColor = 'black';
          aLine.strokeWidth = .6;
      
          //fix horizontal lines being cut off
          if(i === 0){
            aLine.strokeWidth = 2.5;
          }		
          if(i === height){
            aLine.strokeWidth = 2.5;
          }
          //
      
          //horizontal center line
          if(i === 4){
            aLine.strokeWidth = 1.5;
      
            //create increments to space out hatch marks evenly
            var incx = boundingRect.width/40;
      
            //already have yPos
            xPos = boundingRect.left;
            oldyPos = yPos;
            oldxPos = xPos;
            //create hatch marks for horizontal center line
            for(hatchMark = 1; hatchMark <= 40; hatchMark++){
              xPos = oldxPos;
              yPos = oldyPos;
      
              xPos = xPos+hatchMark*incx;
              yPos = yPos*(1-hatchLengthFactor); //make a point to the left of the center line
              hatch1 = new Paper.Point(xPos,yPos);
              yPos=yPos*(1+2*hatchLengthFactor); //make a point to the right of the center line
              hatch2 = new Paper.Point(xPos,yPos);
              connect = new Paper.Path.Line(hatch1,hatch2);
              connect.strokeColor = 'black';
              connect.strokeWidth = .8;
            }
          }
          }
      }
      var numOfWBoxes = 10;
      var numOfHBoxes = 8;
      drawGridLines(numOfWBoxes, numOfHBoxes, Paper.view.bounds);

      // Redraw on resize
      Paper.view.on('resize', function(event) {
        graphLayer.activate();
        graphLayer.removeChildren();
        drawGridLines(numOfWBoxes, numOfHBoxes, Paper.view.bounds);
      });
  
  
  };
  
  export default drawGraph;
