import Paper from "paper";

const clamp = (num, min, max) => Math.min(Math.max(num, min), max);

const drawTriggers = (onUpdate) => {
  let triggerLayer = new Paper.Layer();
  let timePos = 0.5;
  let voltPos = 0.5;
  
  let timeDiff = timePos - 0.5;
  let voltDiff = voltPos - 0.5;

  var numOfWBoxes = 10;
  var numOfHBoxes = 8;

  //Create trigger marks for demo
  var timeTrigger = new Paper.Path({
    segments: [[Paper.view.bounds.left + 5 * Paper.view.bounds.width / numOfWBoxes - 10, Paper.view.bounds.top],
    [Paper.view.bounds.left + 5 * Paper.view.bounds.width / numOfWBoxes + 10, Paper.view.bounds.top],
    [Paper.view.bounds.left + 5 * Paper.view.bounds.width / numOfWBoxes, Paper.view.bounds.top + 10]],
    fillColor: 'black',
    selected: false,
    closed: true
  });
  timeTrigger.fillColor = '#000000';

  var voltTrigger = new Paper.Path({
    segments: [[Paper.view.bounds.left, Paper.view.bounds.top + (4 * Paper.view.bounds.width * (5 / 8) / numOfHBoxes) - 8],
    [Paper.view.bounds.left, Paper.view.bounds.top + 4 * Paper.view.bounds.width * (5 / 8) / numOfHBoxes + 12],
    [Paper.view.bounds.left + 10, Paper.view.bounds.top + 4 * Paper.view.bounds.width * (5 / 8) / numOfHBoxes + 2]],
    fillColor: 'black',
    selected: false,
    closed: true
  });

  timeTrigger.position.x = Paper.view.bounds.width * timePos;
  voltTrigger.position.y = Paper.view.bounds.height * (1 - voltPos);

  // Move Triggers using touch
  var hitOptions = {
    stroke: true,
    fill: true,
    tolerance: 50,
  }

  var timeTriggerActive;
  var voltTriggerActive;
  Paper.view.onMouseDown = function (event) {
    var timeHitResult = timeTrigger.hitTest(event.point, hitOptions);
    timeTriggerActive = timeHitResult && timeHitResult.item;
    var voltHitResult = voltTrigger.hitTest(event.point, hitOptions);
    voltTriggerActive = voltHitResult && voltHitResult.item;
    console.log(timeHitResult, voltHitResult, event.point)
  }

  Paper.view.onMouseDrag = function (event) {
    if (timeTriggerActive) {
      timePos = clamp(event.point.x / Paper.view.bounds.width, 0.01, 0.99);
      timeTrigger.position.x = Paper.view.bounds.width * timePos;
    } else if (voltTriggerActive) {
      voltPos = 1 - clamp(event.point.y / Paper.view.bounds.height, 0.01, 0.99);
      voltTriggerActive.position.y = Paper.view.bounds.height * (1 - voltPos);
    }

    timeDiff = timePos;
    voltDiff = 2*voltPos - 1;


    onUpdate(timeDiff, voltDiff);

  }

  Paper.view.onMouseUp = function (event) {
    timeTriggerActive = null;
    voltTriggerActive = null;
  };

  Paper.view.on('resize', function (event) {
    triggerLayer.activate();
    timeTrigger.remove();
    timeTrigger = new Paper.Path({
      segments: [[Paper.view.bounds.left + 5 * Paper.view.bounds.width / numOfWBoxes - 10, Paper.view.bounds.top],
      [Paper.view.bounds.left + 5 * Paper.view.bounds.width / numOfWBoxes + 10, Paper.view.bounds.top],
      [Paper.view.bounds.left + 5 * Paper.view.bounds.width / numOfWBoxes, Paper.view.bounds.top + 10]],
      fillColor: 'black',
      selected: false,
      closed: true
    });

    timeTrigger.position.x = timePos * Paper.view.bounds.width;

    voltTrigger.remove();
    voltTrigger = new Paper.Path({
      segments: [[Paper.view.bounds.left, Paper.view.bounds.top + (4 * Paper.view.bounds.width * (5 / 8) / numOfHBoxes) - 8],
      [Paper.view.bounds.left, Paper.view.bounds.top + 4 * Paper.view.bounds.width * (5 / 8) / numOfHBoxes + 12],
      [Paper.view.bounds.left + 10, Paper.view.bounds.top + 4 * Paper.view.bounds.width * (5 / 8) / numOfHBoxes + 2]],
      fillColor: 'black',
      selected: false,
      closed: true
    });
    voltTrigger.position.y = (1-voltPos) * Paper.view.bounds.height;

  });

}

export default drawTriggers;

